import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Container,Typography,Grid} from '@material-ui/core';
import logo from '../source/logo.png'

  
const useStyles = makeStyles((theme) => ({
  footer: {
    marginLeft:theme.spacing(-1),
    marginRight:theme.spacing(-1),
    marginBottom:theme.spacing(-20),
    padding: theme.spacing(3, 2),
    marginTop: 'auto',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[800],
  },
  ALL:{
    marginLeft:100,
  },
  typographyBody:{
    fontSize:12,
  },
  typographyTitle:{
    
  },
  typographyFooter:{

  },
  image:{
    maxWidth:200, 
    height:'auto',
  }
}));

export default function Footer() {
  const classes = useStyles();

  return (

    <footer position="absolute" className={classes.footer}>
      
        <Grid container spacing={3} className={classes.ALL}>
          <Grid item xs={8} sm={8}>
            <Grid item xs={8} sm={8}>
              <Typography variant="h4" className={classes.typographyTitle}>
                
              </Typography>
            </Grid>

            <Grid item xs={4} sm={4}>
              <Typography variant="body" className={classes.typographyBody}>
                oops
              </Typography>
            </Grid>
            <Grid item xs={8} sm={8}>
              <Typography className={classes.typographyFooter}>
                :         XXXXX     oop
              </Typography>
            </Grid>
            
          </Grid>
          <Grid item xs={4}>
            <img src={logo} alt= ""  className={classes.image} />
          </Grid>
        </Grid>
      
    </footer>

  );
}